import React from "react";
import Link from "next/link";
import $, { css } from "./style.css";

export default ({ isSubItem, view, isSelected, onSelect = () => {} }) => (
	<Link href="/" as={`/${view == "home" ? "" : view}`} replace>
		<a>
			<b
				className={`${$.item} ${isSelected ? $.is_selected : ""} ${
					isSubItem ? $.sub_menu : ""
				}`}
				onClick={() => {
					Router.push();
					onSelect(view);
				}}
			>
				{view}
			</b>
		</a>
	</Link>
);
